// Card.js
import "./card.css";

export const createCard = () => {
  // Create a card container
  const card = document.createElement("div");
  card.className = "card";

  // Create the image container
  const cardImage = document.createElement("div");
  cardImage.className = "card-image";
  const img = document.createElement("img");
  img.src = "placeholder.png";
  img.alt = "Card image";
  cardImage.appendChild(img);

  // Create the content container
  const cardContent = document.createElement("div");
  cardContent.className = "card-content";

  // Create the title
  const cardTitle = document.createElement("h3");
  cardTitle.className = "card-title";
  cardTitle.textContent = "Title";

  // Create the rating container
  const cardRating = document.createElement("div");
  cardRating.className = "card-rating";

  // Create the stars
  const stars = document.createElement("span");
  stars.className = "stars";
  stars.textContent = "★★★★★";

  // Create the rating text
  const rating = document.createElement("span");
  rating.className = "rating";
  rating.textContent = "4.5 (413)";

  cardRating.appendChild(stars);
  cardRating.appendChild(rating);

  // Create the secondary text
  const cardSecondary = document.createElement("p");
  cardSecondary.className = "card-secondary";
  cardSecondary.textContent = "Secondary text";

  // Create the description
  const cardDescription = document.createElement("p");
  cardDescription.className = "card-description";
  cardDescription.textContent =
    "Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor.";

  // Create the subtitle
  const cardSubtitle = document.createElement("h4");
  cardSubtitle.className = "card-subtitle";
  cardSubtitle.textContent = "Subtitle";

  // Create the items container
  const cardItems = document.createElement("div");
  cardItems.className = "card-items";

  // Create individual items
  const createItem = (text) => {
    const item = document.createElement("span");
    item.className = "card-item";
    item.textContent = text;
    return item;
  };
};
